<template>
  <div id="historyBox">
    <div class="left">
      <el-form v-loading="myLoading" :rules="rules" class="routeForm" ref="temp" :model="temp">
        <!-- <el-radio-group v-model="loadType" @change="changeLoadType">
          <el-radio-button label=""></el-radio-button>
          <el-radio-button label=""></el-radio-button>
        </el-radio-group>-->
        <!-- viewHide -->
        <div class="selectionMode" v-show="viewHide">
          <div @click="changeLoadType(0)" :class="[loadType=='' ? 'isSelectClass' : 'unSelectClass']">
            <div>
              <img src="../../../../static/images/handLineN.png" alt v-if="loadType !==''" />
              <img src="../../../../static/images/handLineY.png" alt v-else />
            </div>
            <span></span>
          </div>
          <div @click="changeLoadType(1)" :class="[loadType!=='' ? 'isSelectClass' : 'unSelectClass']">
            <div>
              <img src="../../../../static/images/drawLineN.png" alt v-if="loadType !==''" />
              <img src="../../../../static/images/drawLineY.png" alt v-else />
            </div>
            <span></span>

          </div>
        </div>
        <el-form-item prop="roadName">
          <!-- <el-input placeholder="" v-model="temp.roadName" :readonly="!viewHide" /> -->
          <el-input placeholder="" v-model="temp.roadName" />
        </el-form-item>
        <el-form-item  class="networkItem">
          <el-select v-model="temp.network" class="filter-item networkSelect" placeholder="" @change="selectChange" multiple filterable collapse-tags>
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item class="networkItem">
          <el-select v-model="temp.schedulingType" class="filter-item networkSelect" placeholder="" filterable collapse-tags>
            <el-option v-for="item in schedulingTypeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item class="networkItem">
          <el-select v-model="temp.schedulingWeekDay" class="filter-item networkSelect" placeholder="" multiple filterable collapse-tags>
            <el-option v-for="item in schedulingWeekDayOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item class="networkItem">
          <el-time-picker v-model="temp.morningTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder=""></el-time-picker>
        </el-form-item>
        <el-form-item class="networkItem">
          <el-time-picker v-model="temp.afternoonTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder=""></el-time-picker>

        </el-form-item>
        <!-- <el-form-item prop="roadName" v-show="!viewHide">
          <el-input v-model="temp.roadName" readonly="" />
        </el-form-item> -->
        <!-- <el-form-item prop="network" class="networkItem" v-show="!viewHide">
          <el-select v-model="temp.network" class="filter-item networkSelect" placeholder="" @change="selectChange" multiple filterable collapse-tags>
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
          <el-form-item  class="networkItem" v-show="!viewHide">
          <el-select v-model="temp.schedulingType" class="filter-item networkSelect" placeholder="" @change="selectChange"  filterable collapse-tags>
            <el-option v-for="item in schedulingTypeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
         <el-form-item  class="networkItem" v-show="!viewHide">
          <el-select v-model="temp.schedulingWeekDay" class="filter-item networkSelect" placeholder="" @change="selectChange" multiple filterable collapse-tags>
            <el-option v-for="item in schedulingWeekDayOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item> -->
        <!-- <el-form-item prop="roadDiscribe">
          <el-input type="textarea" placeholder="" v-model="temp.roadDiscribe "/>
        </el-form-item>-->

        <el-form-item class="btn">
          <el-button type="primary" @click="addRoute('temp')"></el-button>
          <el-button type="primary" @click="goBack"></el-button>
        </el-form-item>
      </el-form>
      <div class="editRoute">
        <el-form v-loading="lineLoading" hide-required-asterisk :inline="true" :rules="lineRule" ref="lineForm" :model="lineForm" v-show="handOrGet">
          <el-form-item prop="devDefault">
            <SelectTree :props="carprops" :options="carList" :setVal.sync="lineForm.devDefault" />
          </el-form-item>

          <el-form-item label="" prop="date">
            <el-date-picker :clearable="false" v-model="lineForm.date" type="date" placeholder="" format="yyyyMMdd" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>

          <el-form-item label="" prop="timeS">
            <el-col :span="11">
              <el-time-picker :clearable="false" v-model="lineForm.timeS" value-format="HH:mm:ss" placeholder=""></el-time-picker>
            </el-col>
          </el-form-item>

          <el-form-item label="" prop="timeE">
            <el-col :span="11">
              <el-time-picker :clearable="false" v-model="lineForm.timeE" value-format="HH:mm:ss" placeholder=""></el-time-picker>
            </el-col>
          </el-form-item>

          <el-button type="primary" class="getLine" @click="getLine('lineForm')"></el-button>
        </el-form>

        <!-- <el-button v-show="showDelete" type="danger" @click="deleteRoute" ></el-button>
         <el-radio-group v-if="loadType==''" class="drawStart" v-model="drawStart" >
          <el-radio class="elRadio" :label="1"></el-radio>
          <el-radio class="elRadio" :label="2"></el-radio>  
        </el-radio-group>-->
      </div>
    </div>
    <!-- <el-button type="primary" @click="goBack" class="mapBtn"></el-button> -->
    <div class="content">
      <div id="historyRouteMap" ref="historyRouteMap"></div>
      <!-- <div class="editRoute">
        <el-form v-loading="lineLoading" hide-required-asterisk :inline="true" :rules="lineRule" ref="lineForm" :model="lineForm" v-show="handOrGet">
          <el-form-item prop="devDefault">
            <SelectTree :props="carprops" :options="carList" :setVal.sync="lineForm.devDefault" />
          </el-form-item>

          <el-form-item label="" prop="date">
            <el-date-picker :clearable="false" v-model="lineForm.date" type="date" placeholder="" format="yyyyMMdd" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>

          <el-form-item label= "" prop="timeS">
            <el-col :span="11">
              <el-time-picker :clearable="false" v-model="lineForm.timeS" value-format="HH:mm:ss" placeholder="" ></el-time-picker>
            </el-col>
          </el-form-item>

          <el-form-item label="" prop="timeE">
            <el-col :span="11">
              <el-time-picker :clearable="false" v-model="lineForm.timeE" value-format="HH:mm:ss" placeholder="" ></el-time-picker>
            </el-col>
          </el-form-item>

         
          <el-button type="primary" class="getLine" @click="getLine('lineForm')"></el-button>
        </el-form>
        
        <el-button v-show="showDelete" type="danger" @click="deleteRoute" ></el-button>
         <el-radio-group v-if="loadType==''" class="drawStart" v-model="drawStart" >
          <el-radio class="elRadio" :label="1"></el-radio>
          <el-radio class="elRadio" :label="2"></el-radio>  
         </el-radio-group>
      </div>-->
      <div class="atMapTopBox" v-show="viewHide">
        <el-button v-show="showDelete" type="danger" @click="deleteRoute"></el-button>
        <el-radio-group v-if="loadType==''" class="drawStart" v-model="drawStart">
          <el-radio class="elRadio" :label="1"></el-radio>
          <el-radio class="elRadio" :label="2"></el-radio>
        </el-radio-group>
      </div>
    </div>
  </div>
</template>
<script>
import rightpng from "../../../../static/images/right.png";
import errorpng from "../../../../static/images/error.png";
import SelectTree from "../../../components/myTree/treeSelect";
import { simplify, create, update } from "../../../api/managementArchives/historyReq";
import { teamAndCarAllBindList } from "../../../api/realTimeMonitoring";
import { carList, getCarList, getCarNoList } from "../../../api/historicalPlayback";
import { Gps2Bd } from "../../../common";
import {
  allDestination, destinationByRoad
} from "../../../api/managementArchives/network";
import mapIcon from "../../../../static/images/mapicon.png";
var formatTime = function (D) {
  // 
  let date = new Date();
  if (D) date = new Date(D);
  let year = date.getFullYear(),
    month1 = date.getMonth() + 1,
    month = month1 > 9 ? month1 : "0" + month1,
    day1 = date.getDate(),
    day = day1 > 9 ? day1 : "0" + day1;
  return `${year}-${month}-${day}`;
};
export default {
  data() {
    var roadNameV = (rule, value, callback) => {
      // 
      if (this.temp.roadName != "") {
        // if (this.pointStrArr.length < 1) {
        //   callback(new Error(""));
        // } else {
        //   callback();
        // }
        callback();
      } else {
        callback(new Error(""));
      }
    },
      networkV = (rule, value, callback) => {
        // 
        if (value.length == 0) {

          callback(new Error(""));

        } else {
          callback();
        }
      },
      linetimeE = (rule, value, callback) => {
        let s = this.lineForm.date + " " + this.lineForm.timeS,
          e = this.lineForm.date + " " + this.lineForm.timeE;
        if (value == "" || value == null) {
          callback(new Error(""));
        } else {
          if (e < s) {
            callback(new Error(""));
          } else {
            callback();
          }
        }
      };
    return {
      isRefresh: false,// 
      map: undefined,
      myLoading: false,
      lineLoading: false,
      carList: [], // 
      getCarImei: {}, // 
      carprops: {
        // 
        // 
        id: "id", //   serialNo
        label: "licenseNo" // 
      },
      loadType: "", // 
      handOrGet: false, // ,false
      showDelete: false, // 
      pointArr: [], // 
      datemin: "", // 
      pickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          this.datemin = minDate.getTime();
          if (maxDate) this.datemin = "";
        },
        disabledDate: now => {
          if (this.datemin != "") {
            // 6
            let day6 = 1000 * 60 * 60 * 24 * 6, // 6
              before6 = this.datemin - day6, // 6
              after6 = this.datemin + day6; // 6
            return now.getTime() < before6 || now.getTime() > after6;
          }
        }
      },
      lineForm: {
        // 
        devDefault: "", // 
        // dateVal:'',// 
        date: formatTime(),
        timeS: "00:00:00",
        timeE: "23:59:59",
      },
      temp: {
        roadName: this.roadName, // 
        roadDiscribe: undefined, // 
        network: [],
        schedulingWeekDay: [1, 2, 3, 4, 5],
        schedulingType: 1,
        afternoonTime: "",
        morningTime: "",
      },
      rules: {
        roadName: [{ validator: roadNameV, trigger: "blur" }],
        network: [{ validator: networkV, trigger: "change" }],

      },
      lineRule: {
        devDefault: [
          { required: true, message: "", trigger: "change" }
        ],
        // dateVal: [
        //   { required: true, message: "", trigger: "blur" }
        // ]
        date: [{ required: true, message: "", trigger: "change" }],
        timeS: [
          { required: true, message: "", trigger: "change" }
        ],
        timeE: [{ required: true, validator: linetimeE, trigger: "change" }]
      },
      lineOptions: {
        // 
        strokeOpacity: 1,
        strokeColor: "#3f9c0f",
        strokeWeight: 4
      },
      markerStart: new BMap.Icon(mapIcon, new BMap.Size(26, 41), {
        anchor: new BMap.Size(13, 42),
        imageOffset: new BMap.Size(-199, -139)
      }),
      markerEnd: new BMap.Icon(mapIcon, new BMap.Size(26, 41), {
        anchor: new BMap.Size(13, 42),
        imageOffset: new BMap.Size(-225, -139)
      }),
      drawStart: 2, //,
      networkOptions: [], //

      schedulingTypeOptions: [
        { id: 1, name: '+' },
        { id: 2, name: '' },
        { id: 3, name: '' },
        { id: 5, name: '' },
        { id: 7, name: '' },
        { id: 0, name: '' }

      ],
      schedulingWeekDayOptions: [
        { id: 1, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
        { id: 4, name: '' },
        { id: 5, name: '' },
        { id: 6, name: '' },
        { id: 7, name: '' }
      ],

      mapvLayer: null,//
      mapvLayer1: null,//

    };
  },
  props: ["geomList", 'roadName', 'roadId', 'schedulingWeekDay', 'schedulingType', 'morningTime', 'afternoonTime'],
  computed: {
    pointStrArr() {
      // 
      return this.pointArr.map(x => `${x.lng} ${x.lat}`);
    },
    viewHide() {// 
      return !this.roadName;
    }
  },
  components: {
    SelectTree
  },
  mounted() {

    this.initMap();
    this.getDevlist();
    this.getSelect()
  },
  methods: {
    selectChange(val) {

      if (this.mapvLayer) {
        this.mapvLayer.destroy();

      }
      if (this.mapvLayer1) {
        this.mapvLayer1.destroy();

      }
      let geom = [], geomDataNetwork = [], geomDataNetwork1 = [];

      //
      for (let i = 0; i < this.temp.network.length; i++) {
        for (let j = 0; j < this.networkOptions.length; j++) {
          if (this.temp.network[i] == this.networkOptions[j].id) {

            geom = this.networkOptions[j].geom.slice(6, -1).split(" ")
            geomDataNetwork.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                },
                text: this.networkOptions[j].name
              })
            geomDataNetwork1.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                }

              })
          }
        }

      }



      let dataSet = new mapv.DataSet(geomDataNetwork);
      let dataSet1 = new mapv.DataSet(geomDataNetwork1);
      let options = {
        draw: 'text',
        avoid: true,
        size: 12,
        font: '18px Arial',
        fillStyle: '#31478F',
        shadowColor: '#31478F',
        shadowBlur: 10
      }
      this.mapvLayer = new mapv.baiduMapLayer(this.map, dataSet, options);
      let options1 = {
        fillStyle: 'rgba(255, 50, 50, 1)',
        shadowColor: 'rgba(255, 50, 50, 1)',
        shadowBlur: 30,
        globalCompositeOperation: 'lighter',
        size: 5,
        draw: 'simple'
      }
      this.mapvLayer1 = new mapv.baiduMapLayer(this.map, dataSet1, options1);

    },
    // 
    getNetworkByRoute() {
      return new Promise((resolve, reject) => {
        destinationByRoad({ routeId: this.roadId })
          .then(res => {

            resolve(res.data)
          }).catch(error => {

          })
          .finally(() => {

          });
      });

    },

    //
    initMap() {
      let _t = this;
      _t.createMap(); //
      _t.setMapEvent(); //
      _t.addMapControl(); //
      _t.showDelete = false;
      _t.pointArr = []; // 
      _t.$nextTick(() => {
        _t.$refs["temp"].resetFields(); // 
      });
      // if (_t.geomList.length == 0) {
      if (_t.viewHide) {
        // 
        if (_t.handOrGet) {
          // 
          _t.$nextTick(() => {
            _t.$refs["lineForm"].resetFields(); // 
          });
        } else {
          _t.handDraw();
        }
      } else {
        // 
        let _t = this,
          map = _t.map,
          geomData = [], geomDataNetwork = [];
        this.temp.schedulingType = this.schedulingType;
        this.temp.morningTime = this.morningTime;
        this.temp.afternoonTime = this.afternoonTime;
        if (this.schedulingWeekDay) {
          // this.temp.schedulingWeekDay=JSON.parse(this.schedulingWeekDay);
          this.$set(this.temp, 'schedulingWeekDay', [...JSON.parse(this.schedulingWeekDay)])
          console.warn(this.temp.schedulingWeekDay)
        } else {
          this.temp.schedulingWeekDay = [];
          // this.$message.warning('')

        }


        this.getNetworkByRoute().then(data => {
          if (!data) {
            data = []
          }
          //
          data.forEach(x => {
            this.temp.network.push(x.id);
            let geom = x.geom.slice(6, -1).split(" ")
            geomDataNetwork.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                },
                text: x.name
              })
            // geomDataNetwork.push(new BMap.Point(geom[0],geom[1]));
          });
          let dataSet = new mapv.DataSet(geomDataNetwork);
          let options = {
            draw: 'text',
            avoid: true,
            size: 12,
            font: '18px Arial',
            fillStyle: '#31478F',
            shadowColor: '#31478F',
            shadowBlur: 10
          }
          new mapv.baiduMapLayer(map, dataSet, options)
          // let pointCollection = new BMap.PointCollection(geomDataNetwork, {
          //   color: "#d340c3",
          //   size: BMAP_POINT_SIZE_SMALL
          // });
          // map.addOverlay(pointCollection);

        })
        // .catch(err => {
        //   this.$message.error('')
        // })
        if (this.geomList.length) {
          this.geomList.forEach(x => {
            geomData.push(new BMap.Point(x.split(" ")[0], x.split(" ")[1]));
          });
          map.setViewport(geomData, { margins: [100, 100, 100, 100] });
          let polyline = new BMap.Polyline(geomData, _t.lineOptions);
          map.addOverlay(polyline);
          // 
          var marker1 = new BMap.Marker(geomData[0], { icon: _t.markerStart }); // 
          map.addOverlay(marker1);
          var marker2 = new BMap.Marker(geomData[geomData.length - 1], {
            icon: _t.markerEnd
          }); // 
          map.addOverlay(marker2);
          //
          // let pointCollection = new BMap.PointCollection(geomData, {
          //   color: "#d340c3",
          //   size: BMAP_POINT_SIZE_SMALL
          // });
          // map.addOverlay(pointCollection);
          _t.pointArr = [...geomData];
          // _t.handDraw();// 
        }

      }
    },
    // 
    createMap() {
      this.map = new BMap.Map("historyRouteMap", { enableMapClick: false }); //
      // this.map.setMapStyle({features:['road','water','land','point','building']});
      // var point = new BMap.Point(113.925973657, 22.5760535462); //
      // this.map.centerAndZoom(point, 13); //
      if (this.geomList.length == 0) {
        // 
        this.map.centerAndZoom("", 13);
      }
      this.map.enableScrollWheelZoom();
    },
    //
    setMapEvent() {
      this.map.enableDragging(); //()
      this.map.enableScrollWheelZoom(); //
      this.map.enableDoubleClickZoom(); //()
      this.map.enableKeyboard(); //
    },
    //
    addMapControl() {
      //
      var ctrl_nav = new BMap.NavigationControl({
        anchor: BMAP_ANCHOR_TOP_LEFT,
        type: BMAP_NAVIGATION_CONTROL_LARGE
      });
      // this.map.addControl(ctrl_nav);
      //
      var ctrl_sca = new BMap.ScaleControl({ anchor: BMAP_ANCHOR_BOTTOM_LEFT });
      var overView = new BMap.OverviewMapControl();

      var mapType1 = new BMap.MapTypeControl({
        mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP],
        anchor: BMAP_ANCHOR_TOP_LEFT
      });
      // this.map.addControl(ctrl_sca);

      this.map.addControl(mapType1); //2D
    },
    //
    delete_control() {
      this.map.removeControl(mapType1); //2D
      this.map.removeControl(overView);
      this.map.removeControl(overViewOpen);
    },
    // 
    getLine(formName) {
      let _t = this;
      _t.map.clearOverlays();
      this.selectChange();
      _t.pointArr = []; // 
      _t.$nextTick(() => {
        _t.$refs[formName].validate(valid => {
          if (valid) {
            _t.lineLoading = true;
            let s = new Date(
              _t.lineForm.date.replace(/-/g, "/") + " " + _t.lineForm.timeS
            ).getTime(),
              e = new Date(
                _t.lineForm.date.replace(/-/g, "/") + " " + _t.lineForm.timeE
              ).getTime();
            // getCarList({
            //   serialNo: _t.getCarImei[_t.lineForm.devDefault],
            //   startTime: s,
            //   endTime: e
            // })
            //
            getCarNoList({
              carNo: _t.getCarImei[_t.lineForm.devDefault],
              startTime: s,
              endTime: e,
              hasAddress: false
            })
              .then(res => {
                // console.log(res)
                if (res.success) {
                  if (
                    res.data == null ||
                    res.data.length == 0 ||
                    res.data.filter(
                      x =>
                        x.lat != null &&
                        x.lng != null &&
                        x.lat * 1 > 1 &&
                        x.lng * 1 > 1
                    ).length == 0
                  ) {
                    _t.$message.warning("");
                    _t.showDelete = false; // 
                  } else {
                    // pointArr
                    // mlng  mlat 
                    _t.showDelete = true; // 
                    let datas = [];
                    datas = res.data
                      .filter(
                        x =>
                          x.lat != null &&
                          x.lng != null &&
                          x.lat * 1 > 1 &&
                          x.lng * 1 > 1
                      )
                      .map(x => {
                        let point = Gps2Bd(new BMap.Point(x.lng, x.lat)); // 
                        return `${point.lng} ${point.lat}`;
                      });
                    // console.log('');
                    // console.log(datas);
                    // 
                    let myData = { wktGps: datas };
                    simplify(myData).then(results => {
                      if (results.success) {
                        // 
                        // console.log(results);
                        let geom = results.data.slice(11, -1).split(",");
                        // console.log('');
                        // console.log(geom);
                        geom.forEach(x => {
                          let newPointArr = x.split(" ");
                          _t.pointArr.push(
                            new BMap.Point(x.split(" ")[0], x.split(" ")[1])
                          );
                        });
                        // console.log( _t.pointArr);
                        _t.$refs.temp.validateField("roadName"); // 
                        _t.map.setViewport(_t.pointArr, {
                          margins: [100, 100, 100, 100]
                        });
                        _t.getNewPoint();
                      } else {
                        _t.$message.error(results.msg);
                      }
                    });
                  }
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
                _t.lineLoading = false;
              });
          }
        });
      });
    },
    // 
    addRoute(formName) {
      let _t = this;
      let req = create;

      _t.$nextTick(() => {
        _t.$refs[formName].validate(valid => {
          if (valid) {
            _t.myLoading = true;
            let myData;
            if (!this.viewHide) {
              req = update;
              myData = {
                id: this.roadId,
                name: _t.temp.roadName,
                // corss: JSON.stringify(_t.temp.network),
                schedulingType: _t.temp.schedulingType
                // schedulingWeekDay:JSON.stringify( _t.temp.schedulingWeekDay),
                // wktGps: _t.pointStrArr
              };
              // if (_t.temp.schedulingWeekDay.length !== 0) {
                myData.schedulingWeekDay = JSON.stringify(_t.temp.schedulingWeekDay)
              // }
                if (_t.temp.network.length) {
                myData.corss =JSON.stringify(_t.temp.network)
              }
              if (_t.temp.morningTime) {
                myData.morningTime = _t.temp.morningTime
              }
              if (_t.temp.afternoonTime) {
                myData.afternoonTime = _t.temp.afternoonTime
              }
            } else {
              myData = {
                name: _t.temp.roadName,
                // crosslobby: JSON.stringify(_t.temp.network),
                schedulingType: _t.temp.schedulingType,

                // wktGps: _t.pointStrArr
              };
               if (_t.temp.network.length) {
                myData.crosslobby =JSON.stringify(_t.temp.network)
              }
              if (_t.temp.schedulingWeekDay.length !== 0) {
                myData.schedulingWeekDay = JSON.stringify(_t.temp.schedulingWeekDay)
              }
              if (_t.pointStrArr.length) {
                myData.wktGps = _t.pointStrArr.length
              }
              if (_t.temp.morningTime !== null) {
                myData.morningTime = _t.temp.morningTime
              }
              if (_t.temp.afternoonTime !== null) {
                myData.afternoonTime = _t.temp.afternoonTime
              }

            }

            req(myData)
              .then(res => {
                if (res.status == 0) {
                  _t.isRefresh = true;
                  let text = '';
                  if (!this.viewHide) {
                    text = '';
                  _t.$message.success(text);
                  this.goBack();
                  return;
                  }
                  _t.$message.success(text);
                  _t.initMap();
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
                _t.myLoading = false;
              });
          }
        });
      });
    },
    changeLoadType(type) {
      // 
      if (type == 0) {
        this.loadType = "";
        this.handOrGet = false;
      } else if (type == 1) {
        this.loadType = "";
        this.handOrGet = true;
      }
      // this.handOrGet = !this.handOrGet;
      this.initMap();
      if (type == 0) {
        this.handDraw();
      }
    },
    handDraw() {
      // 
      let _t = this,
        drawingManager = new BMapLib.DrawingManager(_t.map, {
          //
          enableDrawingTool: true,
          polylineOptions: _t.lineOptions,
          drawingToolOptions: {
            drawingModes: [BMAP_DRAWING_POLYLINE]
          }
        });
      drawingManager.addEventListener("polylinecomplete", function (overlay) {
        // 
        _t.showDelete = true; // 
        // _t.pointArr = overlay.getPath();// 
        if (_t.drawStart == 2) {
          _t.pointArr = _t.pointArr.concat(overlay.getPath());
        } else if (_t.drawStart == 1 && _t.pointArr.length) {
          let reverseArr = [...overlay.getPath()].reverse();
          _t.pointArr = reverseArr.concat(_t.pointArr);
        } else {
          _t.pointArr = overlay.getPath();
        }
        // _t.map.removeOverlay(overlay);// 
        _t.map.clearOverlays();
        _t.selectChange();
        _t.$refs.temp.validateField("roadName"); // 
        _t.getNewPoint();
        // document.getElementsByClassName('BMapLib_Drawing_panel')[0].style.display = 'none';// 
      });
    },
    getNewPoint() {
      // 
      let _t = this,
        map = _t.map,
        count = 0, // --
        marks = [], //  [H{lng:123,lat:456}]--
        clickIndexArr = [], //  [1,6]--
        polyline = undefined, // 
        pointCollection = undefined, // 
        pointCArr = [..._t.pointArr], // copy [H{lng:123,lat:456}]--
        editLine = undefined, // 
        editLineArr = []; //  [H{lng:123,lat:456}]--

      // 
      polyline = new BMap.Polyline(_t.pointArr, _t.lineOptions);
      map.addOverlay(polyline);
      // 
      var marker1 = new BMap.Marker(_t.pointArr[0], { icon: _t.markerStart }); // 
      map.addOverlay(marker1);
      var marker2 = new BMap.Marker(_t.pointArr[_t.pointArr.length - 1], {
        icon: _t.markerEnd
      }); // 
      map.addOverlay(marker2);
      pointCollection = new BMap.PointCollection(_t.pointArr, {
        color: "#d340c3",
        size: BMAP_POINT_SIZE_SMALL
      });
      map.addOverlay(pointCollection);
      // 
      pointCollection.addEventListener("click", function (e) {
        let clickP = e.point, // 
          clickPoint = `${clickP.lng} ${clickP.lat}`, // 
          clickIndex = _t.pointStrArr.indexOf(clickPoint), // 
          markoption = {
            icon: new BMap.Symbol(BMap_Symbol_SHAPE_CIRCLE, {
              scale: 7,
              fillColor: "red",
              fillOpacity: 1,
              strokeWeight: 1
            })
          }; // 
        ///////////////////
        if (marks.length < 2) {
          clickIndexArr.push(clickIndex); // 
          editLineArr.push(clickP); // 
          marks[count] = new BMap.Marker(clickP, markoption); // 
          map.addOverlay(marks[count++]);
        }
        if (count == 2) {
          // ,
          count = 3; // 
          if (clickIndexArr[0] > clickIndexArr[1]) {
            // 
            clickIndexArr.reverse();
            marks.reverse();
            editLineArr.reverse();
          }
          marks[0].setIcon(new BMap.Icon(rightpng, new BMap.Size(16, 16))); // 
          marks[1].setIcon(new BMap.Icon(errorpng, new BMap.Size(16, 16))); // 
          // 
          if (clickIndexArr[0] == 0) {
            marks[0].setOffset(new BMap.Size(12, 12));
          }
          if (clickIndexArr[1] == _t.pointArr.length - 1) {
            marks[1].setOffset(new BMap.Size(12, 12));
          }
          // editLineArr
          editLine = new BMap.Polyline(editLineArr, {
            strokeColor: "red",
            enableEditing: true
          });
          map.addOverlay(editLine); // 
          pointCArr.splice(
            clickIndexArr[0],
            clickIndexArr[1] - clickIndexArr[0] + 1
          ); // 
          pointCollection.setPoints(pointCArr); // 
          // 
          editLine.addEventListener("lineupdate", function (e) {
            editLineArr = e.target.getPath(); // editLineArr
          });

          marks.forEach((x, i) => {
            // 
            x.addEventListener("click", function () {
              if (i == 0) {
                // 
                _t.pointArr.splice(
                  clickIndexArr[0],
                  clickIndexArr[1] - clickIndexArr[0] + 1
                ); // 
                editLineArr.forEach((y, j) => {
                  _t.pointArr.splice(clickIndexArr[0] + j, 0, y); // editLineArr
                });
              }
              map.removeOverlay(marks[0]); // 
              map.removeOverlay(marks[1]);
              map.removeOverlay(editLine); // 
              map.removeOverlay(polyline); // 
              pointCollection.clear(); // 
              count = 0; // 
              marks = [];
              clickIndexArr = [];
              editLineArr = [];
              pointCArr = [..._t.pointArr];
              polyline = new BMap.Polyline(_t.pointArr, _t.lineOptions); // 
              map.addOverlay(polyline);
              pointCollection.setPoints(_t.pointArr); // 
              // console.log('');
              // console.log(_t.pointStrArr);
              // 
              marker1.setPosition(_t.pointArr[0]);
              marker2.setPosition(_t.pointArr[_t.pointArr.length - 1]);
            });
          });
        }
      });
    },
    deleteRoute() {
      // 
      this.map.clearOverlays();
      this.selectChange();
      this.pointArr = [];
      this.showDelete = false;
      let BMapLib_Drawing_panel = document.getElementsByClassName(
        "BMapLib_Drawing_panel"
      )[0];
      if (BMapLib_Drawing_panel) {
        BMapLib_Drawing_panel.style.display = "block"; // 
      }
    },

    getDevlist() {
      // 
      var that = this;
      teamAndCarAllBindList({}).then(res => {
        if (res.status == 0) {
          that.setCarLineOptions(res); // 
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    setCarLineOptions(res) {
      // 
      let t = this,
        result = [],
        resObj = res.data;
      for (let i in resObj) {
        if (resObj[i].length > 0) {
          // 
          // resObj[i].map(x=>x.showName = `${x.licenseNo}(${x.serialNo})`);
          resObj[i].forEach(x => {
            // t.getCarImei[x.licenseNo] = x.serialNo; // 
            t.getCarImei[x.licenseNo] = x.licenseNo; // 
            x.title = x.serialNo;
          });
          result.push({
            // serialNo: i == "" ? "" : i,// showName
            licenseNo: i == "" ? "" : i,
            children: resObj[i]
          });
        }
      }
      this.carList = result;
    },
    goBack() {
      // 
      this.$emit("closePDial", this.isRefresh);
    },
    //
    getSelect() {
      let that = this;

      allDestination({ type: -1 }).then(res => {
        if (res.status == 0) {
          if (res.data.records === null) {
            // that.networkOptions = [{ id: -1, name: "" }];
            return;
          }
          that.networkOptions = res.data;
          // that.networkOptions.unshift({ id: -1, name: "" });
        } else {
          if (res.data == null) {
            this.networkOptions = [];
          }
          this.$message.error(res.msg);
        }
      });



    },
  }
};
</script>
