import request from '../../request'
//
export function roadlist(data) {
  return request({
    url: '/map/roadlist',
    method: 'post',
    data
  })
}
//
export function deletelist(data) {
  return request({
    url: '/map/delete',
    method: 'post',
    data
  })
}
// 
export function viewlist(data) {
  return request({
    url: '/map/complexGeometry',
    method: 'post',
    data
  })
}
// 
export function carRoadList(data) {
  return request({
    url: '/planRoad/list',
    method: 'post',
    data
  })
}
// 
export function carRoadBind(data) {
  return request({
    url: '/planRoad/add',
    method: 'post',
    data
  })
}
// 
export function carRoadUnbind(data) {
  return request({
    url: '/planRoad/remove',
    method: 'post',
    data
  })
}
