export function mileageList(data) {
  return request({
    url: '/statistics/milesrange',
    method: 'post',
    data,
    headers:{'content-type':'application/json'}
  })
}
// 
export function milesRangeByTeam(data) {
  return request({
    url: '/statistics/milesRangeByTeam',
    method: 'post',
    data
  })
}
