import request from '../request'

//()
export function teamAndCarList(data) {
    return request({
        url: '/cars/allList',
        method: 'post',
        data
    })
}

export function teamAndCarAllBindList(data) {
    return request({
        url: '/cars/allBindList',
        method: 'post',
        data
    })
}

//
export function teamAndGunList(data) {
    return request({
        url: '/guns/allList',
        method: 'post',
        data
    })
}
//
export function logsDelay(data) {   
    return request({
        url: '/logs/delays',
        method: 'post',
        data
    })
}
//
export function lastgps(data) {
    return request({
        url: '/map/lastgps',
        method: 'post',
        // params:data,
        data,
        headers:{'content-type':'application/json'},
    })
}
