import request from '../../request'
//
export function simplify(data) {
  return request({
    url: '/map/simplify',
    method: 'post',
    data,
    headers:{'content-type':'application/json'}
  })
}
//
export function create(data) {
  return request({
    url: '/map/create',
    method: 'post',
    data,
    headers:{'content-type':'application/json'}
  })
}

//
export function update(data) {
  return request({
    url: '/map/modifyCross',
    method: 'post',
    data
  })
}
